.navigation-custom {
  transition: transform 0.4s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  transform: translateX(100%);
  background: rgba(0, 0, 0, 0.75);
  z-index: 3; }
  .navigation-custom.menu-open {
    transform: translateX(0%); }
  .navigation-custom .wrap {
    position: absolute !important;
    top: 50%;
    left: 50%;
