@import "slick";
@import "slick-theme";
#page-loader {
  position: fixed;
  display: block;
  background: rgba(0, 0, 0, 0.9);
  color: #fff;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 3;
  strong {
    position: absolute;
    font-size: 50px;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

  }
}

.page.page-one-column:not(.twentyseventeen-front-page) #primary {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
}

//style the navigation trigger aka burger menu
.nav-trigger {
  a {
    background-color: #000;
    display: inline-block;
    position: relative;
    padding: 5px;
    span {
      display: block;
      position: relative;
      margin: 0;
      padding: 0;
      width: 40px;
      height: 5px;
      background: #fff;
      margin-bottom: 5px;
      border-radius: 3px;
    }
  }

}

.js {
  #menu-toggle {
    display: none;
  }
  .main-navigation {
    ul {
      display: block;
      background: none;
      border-top: none;
    }
    @media screen and(max-width: 567px) {
      ul.sub-menu {
        display: none;
      }
    }
  }
}

//this is the main navigation container
.navigation-custom {
  transition: transform 0.4s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  transform: translateX(100%);
  background: rgba(0, 0, 0, 0.75);
  z-index: 3;
  &.menu-open {
    transform: translateX(0%);
  }
  .wrap {
    position: absolute !important;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: none;
    padding: 0;
    margin: 0;
    color: #fff;
    #top-menu {
      li {
        display: block;
        text-align: left;
        font-size: 2rem;
        &.menu-item-has-children {
          ul.sub-menu {
            display: none;
            position:relative;
            top:0;
            left:inherit;
          }
          &:hover {
            ul.sub-menu {
              display: inline-block;
            }
          }
        }
        a {
          color: rgba(255, 255, 255, 0.9);
          padding: 0;
          margin: 0 0 10% 0;
          &:hover {
            color: rgba(255, 255, 255, 1);
          }
        }
      }
    }
  }
  .close-menu {
    position: absolute;
    top: 10%;
    right: 10%;
    font-size: 2rem;
    a {
      color: #fff;
      &:focus {
        outline: none;
      }
    }
  }
}

//for the ajax content search
.digthis-list-container {
  /*
  *Pagination Design
  */
  div.pagination {
    list-style: none;
    padding: 0;
    .page-numbers {
      li {
        display: inline-block;
      }
    }
  }
  /*Search / Form Filters*/
  .filter-items {
    form {
      margin-bottom: 20px;
      .search {
        display: inline-block;
        width: 80%;
      }
      input {
        font-size: 1rem;
        line-height: 2rem;
        padding: 10px;
      }
      input[type="submit"] {
        width: 18%;
        display: inline-block;
      }
    }
  }
  /* Contains the query results*/
  .item-container {
    width: 100%;
    padding: 0 auto;
    &:hover {
      .item-details {
        display: block;
      }
    }
    &:after {
      clear: both;
      content: ' ';
      display: block;
    }
    .list-item {
      float: left;
      position: relative;
      width: 32%;
      margin-top: 10px;
      margin-right: 1.33%;
      min-height: 300px;
      background: #ccc;
      @media screen and (max-width: 567px) {
        width: 100%;
      }
      .item-image {
        line-height: 0;
        img {
          margin: 0;
          padding: 0;
        }
      }
      .item-details {
        background: rgba(0, 0, 0, 0.5);
        display: block;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 2;
        * {
          text-align: center;
          margin: 0 auto;
        }
        h2 {
          margin-top: 30%;
          color: #fff;
        }
        a {
          margin-top: 10%;
          display: block;
          color: #fff;
        }
      }
    }
    .nothing-found {
      h3 {
        text-align: center;
      }
    }

  }

}

#back-to-top {
  position: fixed;
  z-index: 2;
  bottom: 10%;
  right: 10%;
  background: #000;
  color: #fff;
  padding: 10px;
  display: none;
}

.digthis-slick-slider{
  background: #000;
  .slick-arrow{
    z-index:3;
  }
  .slick-next{
    right:0;
  }
  .slick-prev{
    left:0;
  }
}